
@keyframes wobble {
0% {
    transform: rotate(0deg);
}
20% {
    transform: rotate(10deg);
}
50% {
    transform: rotate(-10deg);
}
100% {
    transform: rotate(0deg);
}
}
@keyframes bounce {
from, 20%, 53%, 80%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0);
}
10% {
    transform: rotate(-15deg);
}
20% {
    transform: rotate(15deg);
}
40%, 43% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -18px, 0);
}
70% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -8px, 0);
}
90% {
    transform: translate3d(0, -1px, 0);
}
}
.die {
  transition: padding 0.5s ease;
  height: 80px;
  padding: 50px 0 0 0;
  position: relative;
  width: 80px;
}
.die .st0 {
    fill: #F4EFEF;
    stroke: #000000;
    stroke-width: 11;
    stroke-miterlimit: 10;
}
.die .dot {
    display: none;
}
.die.rolling {
    padding: 0 0 50px 0;
}
.die.rolling svg {
      animation: wobble 0.5s infinite;
}
.die.stop-rolling svg {
    animation: bounce 0.5s 1;
    animation-delay: 0.17s;
}
.die.result-1 #three {
    display: block;
}
.die.result-2 #one,
  .die.result-2 #seven {
    display: block;
}
.die.result-3 #one,
  .die.result-3 #three,
  .die.result-3 #seven {
    display: block;
}
.die.result-4 #one,
  .die.result-4 #four,
  .die.result-4 #five,
  .die.result-4 #seven {
    display: block;
}
.die.result-5 #one,
  .die.result-5 #three,
  .die.result-5 #four,
  .die.result-5 #five,
  .die.result-5 #seven {
    display: block;
}
.die.result-6 #one,
  .die.result-6 #two,
  .die.result-6 #four,
  .die.result-6 #five,
  .die.result-6 #six,
  .die.result-6 #seven {
    display: block;
}
.die:after {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    content: ' ';
    display: block;
    height: 2px;
    left: 5px;
    position: absolute;
    right: 5px;
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
}
main {
  font-family: 'Dosis', sans-serif;
  font-size: 18px;
  text-align: center;
}
h1 {
  font-family: 'Diplomata SC', cursive;
  font-size: 46px;
}
.dice-form {
  margin: 0 auto;
  padding: 20px 0;
  overflow: hidden;
}
.dice-form > div {
    display: inline-block;
    padding: 0 5px;
    vertical-align: middle;
}
.dice-form input {
    color: #777;
    border: 1px solid #EEE;
    border-radius: 2px;
    font-size: 22px;
    text-align: center;
    width: 50px;
}
.dice-form input:active, .dice-form input:hover, .dice-form input:focus {
      border: 1px solid #999;
}
.dice-form button {
    border-style: solid;
    border-width: 0px 0px 3px;
    box-shadow: 0 -1px 0 rgba(255, 255, 255, 0.1) inset;
    color: #555;
    border-radius: 4px;
    cursor: pointer;
    display: inline-block;
    font-style: normal;
    overflow: hidden;
    text-align: center;
    text-decoration: none;
    text-overflow: ellipsis;
    transition: all 200ms ease-in-out 0s;
    white-space: nowrap;
    padding: 5px 49px 6px;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 2px;
    border-color: #DDD;
    background-color: #EEE;
}
.dice-form button:hover {
      border-color: #BBB;
      background-color: #CCC;
}
.dice-form button:active {
      background-color: #AAA;
      color: #FFF;
}
.dice-results {
  height: 200px;
  padding-top: 20px;
}
.dice-results .results-list {
    display: inline-block;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}
.dice-results .results-list li {
      display: block;
      float: left;
      margin: 5px;
}



